<template>
	<!-- 0 判断  1 单选  2 定项多选  3 简答   4 填空   5 不定项多选 -->
	
		<view class="page">
			<uni-nav-bar @clickLeft="back" left-icon="back" :border="false" title="错题详情" statusBar>
				<view slot="right" @click="deletepage">
					<image class="delete-icon" src="../../static/wrong_delete.png" mode=""></image>
				</view>
			</uni-nav-bar>

			<template v-if="type == 0">
				<scroll-view class="page" :style="{height : scrollH + 'px'}" scroll-y="true">
					<view class="top">
						<image @click="showbig_top_top(imagepath)" class="topimage" :src="imagepath" mode="aspectFill"></image>
					</view>
				
				<view v-show="ishideanswer == true" class="answer_title" @click="chakanjiexi">
					<view class="wrong_text">查看解析</view>
					<image class="wrong_chakan" src="../../static/wrong_chakan.png"></image>
				</view>
				
					<view v-show="ishideanswer == false" class="daan_title">
						<image class="wrong_chakan" src="../../static/wrong_daan.png"></image>
						<view class="daan">答案</view>
					</view>

					<view v-show="ishideanswer == false" v-for="(item,index) in answer_image_group" class="group">
						<image @click="showbig_top(answer_image_group)" class="answer_image" :src="item" mode="aspectFill"></image>
					</view>
					<view  v-show="ishideanswer == false" class="daan_title">
						<image class="wrong_chakan" src="../../static/wrong_beizhu.png"></image>
						<view class="daan">备注</view>
					</view>
					<view v-show="ishideanswer == false" class="beizhu_image">
						<view class="beizhu_textarea">{{des}}</view>
					</view>
				</scroll-view>
			</template>

			<template v-else>
				<view class="message">
					<view v-if="dic.content.type == 0">
						<view class="list_title">
							<text class="itemtype">（判断题）</text><text>{{dic.content.title}}</text>
						</view>
					</view>
					<view v-if="dic.content.type == 1 || dic.content.type == 2 || dic.content.type == 5">
						<view class="list_title">
							<text v-show="dic.content.type == 1" class="itemtype">（单选）</text><text v-show="dic.content.type == 1">{{dic.content.title}}</text>
							<text v-show="dic.content.type == 2" class="itemtype">（定项多选）</text><text v-show="dic.content.type == 2">{{dic.content.title}}</text>
							<text v-show="dic.content.type == 5" class="itemtype">（不定项多选）</text><text v-show="dic.content.type == 5">{{dic.content.title}}</text>
							<view v-for="(item2,index2) in dic.content.ans" class="ans">
								<view>{{ABCD[index2] + '.'+ item2}}</view>
							</view>
						</view>
					</view>
					<view v-if="dic.content.type == 3">
						<view class="list_title">
							<text class="itemtype">（简答题）</text><text>{{dic.content.title}}</text>
						</view>
					</view>
					<view v-if="dic.content.type == 4">
						<view class="list_title">
							<text class="itemtype">（填空题）</text><text>{{dic.content.title}}</text>
						</view>
					</view>
					<view class="audio">
						<template v-if="dic.content.t_img">
							<view class="conect_image">
								<image :src="dic.content.t_img" class="conect_image"></image>
							</view>
						</template>
						<template v-if="dic.content.t_audio">
							<view v-if="dic.content.t_audio.length >0">
								<video id="myVideo" :src="dic.content.t_audio" class="hidden" autoplay="false" @timeupdate="timeupdate" ref="video"
								 @loadedmetadata="loadedmetadata"></video>
								<view class="shadow">
									<view class="slider-box">
										<image :src="buttonimage" class="button" @click="pause" mode="aspectFit"></image>
										<text class="mm">{{start}}</text>
										<slider class="slider" @change="sliderChange" @changing="sliderChanging" block-size="12" :min="0" :max="duration"
										 :value="currentTime" activeColor="#64D3AD" />
										<text class="ss">{{end}}</text>
									</view>
								</view>
							</view>
						</template>
						<template v-if="dic.content.t_video_img">
							<view v-if="dic.content.t_video_img.length >0" class="t_img2" @click="playvideo(dic.content.t_video)">
								<image :src="dic.content.t_video_img" class="t_img" mode="aspectFill">
								</image>
								<image src="../../static/voice.png" class="t_img_play"></image>
							</view>
						</template>
					</view>
				</view>

				<view class="space"></view>
				<view v-show="ishideanswer == true" class="answer_title" @click="chakanjiexi">
					<view class="wrong_text">查看解析</view>
					<image class="wrong_chakan" src="../../static/wrong_chakan.png"></image>
				</view>
				<view v-show="ishideanswer == false" class="daan_title">
					<image class="wrong_chakan" src="../../static/wrong_daan.png"></image>
					<view class="daan"> 答案</view>
				</view>

				<view v-show="ishideanswer == false" class="answer">
					<view class="youranswer">你的回答</view>
					<view v-if="dic.content.type == 0">
						<view v-if="dic.answer.rs_user.rs == 0">错</view>
						<view v-if="dic.answer.rs_user.rs == 1">对</view>
					</view>
					<view v-if="dic.content.type == 1">
						<view v-if="dic.answer.rs_user.rs == 0">A</view>
						<view v-if="dic.answer.rs_user.rs == 1">B</view>
						<view v-if="dic.answer.rs_user.rs == 2">C</view>
						<view v-if="dic.answer.rs_user.rs == 3">D</view>
					</view>
					<view v-if="dic.content.type == 2 || dic.content.type == 5" class="danan_xuanze">
						<view v-for="(item,index) in dic.answer.rs_user.rs">
							<view v-if="item == 0">A</view>
							<view v-if="item == 1">B</view>
							<view v-if="item == 2">C</view>
							<view v-if="item == 3">D</view>
						</view>

					</view>
					<view v-if="dic.content.type == 3">
						<view>{{dic.answer.rs_user.rs}}</view>
					</view>
					<view v-if="dic.content.type == 4">
						<view v-for="(item,index) in dic.answer.rs_user.rs" class="tiankong">
							<view>{{index + '.' + item.rs}}</view>
						</view>


					</view>
					<view class="youranswer">正确答案</view>
					<view v-if="dic.content.type == 0">
						<view v-if="dic.answer.rs == 0">错</view>
						<view v-if="dic.answer.rs == 1">对</view>
					</view>
					<view v-if="dic.content.type == 1">
						<view v-if="dic.answer.rs == 0">A</view>
						<view v-if="dic.answer.rs == 1">B</view>
						<view v-if="dic.answer.rs == 2">C</view>
						<view v-if="dic.answer.rs == 3">D</view>
					</view>
					<view v-if="dic.content.type == 2 || dic.content.type == 5" class="danan_xuanze">
						<view v-for="(item,index) in dic.answer.rs">
							<view v-if="item == 0">A</view>
							<view v-if="item == 1">B</view>
							<view v-if="item == 2">C</view>
							<view v-if="item == 3">D</view>
						</view>

					</view>
					<view v-if="dic.content.type == 3">
						<view>{{dic.answer.rs}}</view>

					</view>
					<view v-if="dic.content.type == 4" class="tiankong">
						<view v-for="(item,index) in dic.answer.rs">
							<view>{{index + '.' + item[0]}}</view>

						</view>

					</view>
				</view>
			</template>

	<template v-if="status == 0">
			<view class="bottom">
				<view @click="zhangwo" class="cuotiben">标记为掌握</view>
			</view>
		</template>
		</view>

</template>

<script>
	import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';

	const app = getApp();
	export default {
		components: {
			uniNavBar
		},
		data() {
			return {
				dic: {},
				type: 0,
				ABCD: ['A', 'B', 'C', 'D'],
				duration: '',
				currentTime: 0,
				videoContext: '',
				buttonimage: '',
				status: 0,
				start: '00:00',
				end: '00:00',
				ishideanswer: true,
				id: '',
				scrollH: 0,
				imagepath: '',
				answer_image_group: []
			}
		},
		onLoad(option) {

			this.videoContext = uni.createVideoContext('myVideo');
			this.type = option.type;
			this.id = option.id;
			this.status = option.status;
			let gData = app.globalData;
			var that = this;
			uni.getSystemInfo({
				success: (res) => {
					if (this.type == 0) {
						this.scrollH = res.windowHeight - 100;
						if (this.status == 0) {
							this.scrollH = res.windowHeight - 160;
						} else {
							this.scrollH = res.windowHeight - 100;
						}
					} else {
						this.scrollH = res.windowHeight - 60;
						if (this.status == 0) {
							this.scrollH = res.windowHeight - 90;
						} else {
							this.scrollH = res.windowHeight - 60;
						}
					}
				}
			});
			this.buttonimage = '../../static/voice.png';
			setTimeout(() => {
				uni.request({
					url: gData.site_url + 'Wrongbook.GetInfo',
					method: 'GET',
					data: {
						'id': option.id,
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token
					},
					success: res => {
						if (res.data.data.code == 700) {
							uni.navigateTo({
								url: '../login/login'
							})
							return;
						}
						that.dic = res.data.data.info[0];
						if (that.type == 0) {
							that.imagepath = that.dic.content;
							that.answer_image_group = that.dic.answer;
							that.des = that.dic.des;
						}
						// console.log(JSON.parse(JSON.stringify(res.data.data.info[0])));
						console.log(JSON.parse(JSON.stringify(that.dic)));
					}
				});
			}, 0);
		},
		methods: {
			showbig_top_top(image){
				let imgsArray = [];
				imgsArray.push(image);
				uni.previewImage({
					current: 0,
					urls: imgsArray,
					loop: false
				});
			},
			showbig_top(imagegroup){
				let imgsArray = [];
				for (let i =0;i<imagegroup.length;i++){
						imgsArray.push(imagegroup[i]);
				}
	
				uni.previewImage({
					current: 0,
					urls: imgsArray,
					loop: false
				});
			},
			zhangwo() {
				let gData = app.globalData;
				uni.request({
					url: gData.site_url + 'Wrongbook.SetGrasp',
					method: 'GET',
					data: {
						'id': this.id,
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token
					},
					success: res => {
						if (res.data.data.code == 700) {
							uni.navigateTo({
								url: '../login/login'
							})
							return;
						}
						if (res.data.data.code == 0) {
							uni.showToast({
								title: '标记成功',
								icon: 'none'
							});
							this.status = 1;
						}
					}
				});
			},
			deletepage() {
				let gData = app.globalData;
				uni.request({
					url: gData.site_url + 'Wrongbook.Del',
					method: 'GET',
					data: {
						'id': this.id,
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token
					},
					success: res => {
						if (res.data.data.code == 700) {
							uni.navigateTo({
								url: '../login/login'
							})
							return;
						}
						if (res.data.data.code == 0) {
							uni.showToast({
								title: '删除成功',
								icon: 'none'
							});
							setTimeout(() => {
								uni.navigateBack({
									delta: 1
								});
							}, 0);
						}
					}
				});
			},
			back() {
				uni.navigateBack({

				});
			},
			chakanjiexi() {
				this.ishideanswer = false;

			},
			playvideo(url) {
				console.log(url);
				uni.navigateTo({
					url: '../homework_info/home_video?t_video=' + url + '&type=' + 1,
				});
			},
			pause() {
				if (this.status == 0) {
					this.status = 1;
					this.videoContext.play();
					this.buttonimage = '../../static/voice2.png';
				} else {
					this.status = 0;
					this.videoContext.pause();
					this.buttonimage = '../../static/voice.png';
				}
			},
			timeupdate(event) {
				if (event.detail.detail) {
					this.currentTime = event.detail.detail.currentTime;
					this.duration = event.detail.detail.duration;
					this.start = this.calcTimer(this.currentTime);
					this.end = this.calcTimer(this.duration);
				} else {
					this.currentTime = event.detail.currentTime;
					this.duration = event.detail.duration;
					this.start = this.calcTimer(event.detail.currentTime);
					this.end = this.calcTimer(event.detail.duration);
				}
			},
			// 拖动进度条
			sliderChange(data) {
				this.videoContext.seek(data.detail.value);
			},
			//拖动中
			sliderChanging(data) {
				this.currentTime = data.detail.value;
				this.start = this.calcTimer(data.detail.value);
			},
			loadedmetadata(data) {
				this.duration = data.detail.duration;
				this.start = this.calcTimer(data.detail.duration);
				console.log('音频加载完成');
			},
			calcTimer(timer) {
				if (timer === 0 || typeof timer !== 'number') {
					return '00:00'
				}
				let mm = Math.floor(timer / 60)
				let ss = Math.floor(timer % 60)
				if (mm < 10) {
					mm = '0' + mm
				}
				if (ss < 10) {
					ss = '0' + ss
				}
				return mm + ':' + ss
			}
		}
	}
</script>

